nav .nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 40px;
}

nav .nav .nav_phone_burger {
  display: flex;
  gap: 12px;
  padding: 11px;
}

nav .nav .nav_logo {
  display: flex;
  gap: 15px;
  justify-content: center;
  align-items: center;
}

nav .nav .burger__menu {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  align-content: space-between;
  width: 30px;
  height: 20px;
  padding: 12px;
}

nav .nav .burger__menu .burger {
  display: block;
  background: #000000;
  width: 100%;
  height: 2px;
  border-radius: 4px;
  transition: 0.3s;
}

nav .nav .burger__menu .burger :hover {
  color: rgba(187, 68, 68, 0.808);
}

nav .nav .burger__menu.active .burger:nth-child(1) {
  transform: rotate(-45deg) translate(-11px, 1px);
}

nav .nav .burger__menu.active .burger:nth-child(2) {
  display: none;
}

nav .nav .burger__menu.active .burger:nth-child(3) {
  transform: rotate(45deg) translate(-11px, -2px);
}

nav .nav .nav_menu {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 45px;
}

nav .nav .nav_menu .nav__menu__item a {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  text-decoration: none;
  cursor: pointer;
  color: #000000;
}

nav .nav .nav_menu .nav__menu__item a a:hover {
  color: #6E9C9F;
  font-weight: 600;
}

nav .nav .nav_phone {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 67px;
}

nav .nav .nav_phone .nav_phone_logo {
  display: flex;
  gap: 12px;
}

.couterner {
  padding: 40px;
  margin-top: 40px;
  margin-bottom: 16px;
}

header .header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

header .header .header_left .header_h2 {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 500;
  font-size: 55px;
  color: #000000;
}

header .header .header_left .header_p {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  color: #000000;
  max-width: 385px;
  margin: 45px 0px;
}

header .header .header_left .botten_header {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 243px;
  gap: 9px;
  height: 68px;
  background: rgba(110, 156, 159, 0.1);
}

header .header .header_left .botten_header .header_svg {
  padding: 18px;
}

header .header .header_left .botten_header .header_link {
  padding: 20.51px 28px;
  background: #6E9C9F;
}

header .header .header_left .botten_header .header_link a {
  color: #FFFFFF;
  font-weight: 400;
  font-size: 17px;
  text-decoration: none;
}

header .header .header_right {
  display: flex;
  position: relative;
}

header .header .header_right .header_imgs {
  width: 434px;
  height: 652px;
}

header .header .header_right .header_imgs img {
  width: inherit;
  height: inherit;
}

header .header .header_right .header_imgs1 {
  left: -135px;
  top: 412px;
  width: 179px;
  height: 239px;
  position: absolute;
}

header .header .header_right img {
  width: inherit;
  height: inherit;
}

header .header .header_right .header_imgs2 {
  width: 169px;
  height: 153px;
  right: -36px;
  top: 100px;
  position: absolute;
}

header .header .header_right .header_imgs2 img {
  width: inherit;
  height: inherit;
}

.couterner {
  padding: 40px;
  margin-top: 40px;
  margin-bottom: 16px;
}

@media screen and (max-width: 1110px) {
  nav .nav .nav {
    padding: 0px 80px;
  }

  header .header {
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
    gap: 12px;
  }

  header .header .header_left {
    text-align: center;
    margin: 0 auto;
  }

  header .header .header_left .botten_header {
    margin: auto;
  }

  header .header .header_right .header_imgs {
    width: 100%;
    height: 100%;
  }
}

@media screen and (max-width: 956px) {
  nav .nav {
    margin-top: 10px;
    position: relative;
  }

  nav .nav .nav_menu {
    display: block;
    position: absolute;
    flex-direction: column;
    background: #fff;
    width: 100%;
    top: -5000px;
    left: 0;
    gap: 0px;
    z-index: 5;
    transition: all 0.2s;
  }

  nav .nav .nav_menu.nav__menu__active {
    top: 85px;
  }

  nav .nav .nav_menu .nav__menu__item {
    border-bottom: 0.5px solid #eee;
  }

  nav .nav .nav_menu .nav__menu__item a {
    display: block;
    padding: 32px;
    transition: 0.3s;
  }

  nav .nav .nav_menu .nav__menu__item a::before {
    display: none;
  }

  nav .nav .nav_menu .nav__menu__item a:hover {
    background: #eee;
  }

  nav .nav .nav_menu .nav__menu__item:nth-child(4) {
    border-bottom: none;
  }

  nav .nav .burger__menu {
    display: flex;
    cursor: pointer;
  }
}

@media screen and (max-width: 600px) {
  nav .nav .nav__menu {
    display: block;
    position: absolute;
    flex-direction: column;
    background: #fff;
    width: 100%;
    top: -5000px;
    left: 0;
    gap: 0px;
    z-index: 5;
    transition: all 0.2s;
  }

  nav .nav .nav__menu.nav__menu__active {
    top: 85px;
  }

  nav .nav .nav__menu .nav__menu__item {
    border-bottom: 0.5px solid #eee;
  }

  nav .nav .nav__menu .nav__menu__item a {
    display: block;
    padding: 32px;
    transition: 0.3s;
  }

  nav .nav .nav__menu .nav__menu__item a::before {
    display: none;
  }

  nav .nav .nav__menu .nav__menu__item a:hover {
    background: #eee;
  }

  nav .nav .nav__menu .nav__menu__item:nth-child(4) {
    border-bottom: none;
  }

  nav .nav .nav_phone .nav_phone_logo {
    display: none;
  }

  .nav {
    padding: 12px 40px;
  }

  .nav .nav_phone {
    gap: 15px;
  }

  .nav .nav_phone .nav_phone_logo svg {
    width: 17px;
    height: 20px;
  }

  .nav .nav_phone .nav_phone_logo .nav_number {
    display: none;
  }

  header .header .header_left {
    margin: 0;
  }

  header .header .header_left .header_h2 h2 {
    max-width: 180px;
  }

  header .header .header_right .header_imgs1 {
    display: none;
  }

  header .header .header_right .header_imgs2 {
    display: none;
  }
}